import { createSlice } from '@reduxjs/toolkit'; // 1.

export const counterSlice = createSlice({
  // 2.
  name: 'counter', // 3.
  initialState: {
    // 4.
    value: 0,
  },
  reducers: {
    // 5.
    increment: (state) => {
      state.value += 1;
    },
    decrement: (state) => {
      state.value -= 1;
    },
    incrementByAmount: (state, action) => {
      state.value += action.payload; // 6.
    },
  },
});

export const { increment, decrement, incrementByAmount } = counterSlice.actions; // reducers action을 내보낸다
export default counterSlice.reducer; // counterSlice export
export const selectCount = (state) => state.counter.value; // 9.
