import axios from 'axios';
import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';

export const wList = createAsyncThunk('GET_WORK', async () => {
  //const response = await axios.get("/work/list.json");
  //return response.data;
  return axios
    .get('/data/work.json') // "proxy": "http://live337.cafe24.com"
    .then((res) => res.data)
    .catch((error) => error);
});

export const workSlice = createSlice({
  name: 'work',
  initialState: [],
  reducers: {},
  extraReducers: {
    [wList.fulfilled]: (state, { payload }) => [...payload],
  },
});

export default workSlice.reducer;
