import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { motion } from 'framer-motion';
import { wList } from 'store/WorkReducer';

const WorkList = ({ ulRef, currentRef, activeNum, obj }) => {
  const dispatch = useDispatch();
  const workList = useSelector((state) => state.work);

  const { thumX, thumY, tX, tY } = obj;

  const url = (e) => {
    if (e === '') {
      return alert('오픈 준비중입니다');
    }
    window.open(e);
  };

  useEffect(() => {
    dispatch(wList());
  }, []);

  return (
    <ul ref={ulRef}>
      {workList.map((site, idx) => {
        const active = idx === activeNum;
        const { link, thum, year, title, description, tools, copy } = site;
        return (
          <li
            className={`${active ? 'active' : ''}`}
            ref={active ? currentRef : null}
          >
            <a
              href
              onClick={() => url(link)}
              rel="noopener noreferrer"
              style={{ perspective: 400 }}
            >
              <figure>
                <motion.div
                  style={active ? { translateX: thumX, translateY: thumY } : {}}
                >
                  <img src={thum} alt="" />
                </motion.div>
              </figure>
              <motion.div
                style={active ? { translateX: tX, translateY: tY } : {}}
              >
                <dl>
                  <dt>
                    {' '}
                    <i>{year}</i> {title}{' '}
                  </dt>
                  <dd className="text">
                    {description.map((text, i) => (
                      <span key={i}>{text}</span>
                    ))}
                  </dd>
                  <dd className="tools"> {tools} </dd>
                </dl>
              </motion.div>
            </a>
            <span className="copy">{copy}</span>
          </li>
        );
      })}
    </ul>
  );
};

export default WorkList;
