import React, { useState, useRef, useEffect } from 'react';
import {
  Route,
  Switch,
  Redirect,
  useLocation,
  useHistory,
} from 'react-router-dom';
import { motion, AnimatePresence, useMotionValue } from 'framer-motion';
import Header from 'components/layout/Header';
import Main from 'pages/main/index';
import Work from 'pages/work/index';
import History from 'pages/history/index';

const App = () => {
  const history = useHistory();
  const location = useLocation();
  const x = useMotionValue(100);
  const y = useMotionValue(100);
  const [path, setPath] = useState('');

  const handleMouse = (event) => {
    x.set(event.pageX);
    y.set(event.pageY);
  };

  useEffect(() => {
    if (history.pathname === '/' || location.pathname === '/') {
      setPath('isMain');
    } else if (
      history.pathname === '/history' ||
      location.pathname === '/history'
    ) {
      setPath('isHistory');
    } else if (history.pathname === '/work' || location.pathname === '/work') {
      setPath('isWork');
    }
  }, [history, location]);

  return (
    <motion.div
      className={'rootWrap'}
      style={{ perspective: 3600, height: '100%' }}
      onMouseMove={handleMouse}
    >
      <Header pathClassName={path} />
      <AnimatePresence>
        <Switch location={location} key={location.pathname}>
          <Route exact path="/" component={Main} />
          <Route exact path="/work" render={() => <Work x={x} y={y} />} />
          <Route
            exact
            path="/history"
            render={() => <History pathClassName={path} />}
          />
          <Redirect path="*" to="/" />
        </Switch>
      </AnimatePresence>
    </motion.div>
  );
};

export default App;
